<template>
    <div class="container">
      <el-breadcrumb separator-class="el-icon-arrow-right">
        <el-breadcrumb-item>任务</el-breadcrumb-item>
        <el-breadcrumb-item>需求提交</el-breadcrumb-item>
        <el-breadcrumb-item>需求列表</el-breadcrumb-item>
      </el-breadcrumb>
      <div class="tools">
        <label class="title" for="statusSelect">状态：</label>
        <el-select v-model="statusValue" id="statusSelect" class="selected" @change="statusChange">
          <el-option
            v-for="item in statusOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <label class="title" for="degreeSelect">紧急程度：</label>
        <el-select v-model="degreeValue" id="degreeSelect" class="selected" @change="degreeChange">
          <el-option
            v-for="item in degreeOptions"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-button type="primary" class="addItem" @click="addTask()">新增需求</el-button>
      </div>
      <div class="tableContainer">
        <el-table
          :data="tableData"
          height="100%"
          border
          style="width: 100%" class="table">
          <el-table-column type="expand">
            <template slot-scope="props">
              <el-form label-position="left" inline class="demo-table-expand">
                <el-form-item label="任务id">
                  <span>{{ props.row.task_id }}</span>
                </el-form-item>
                <el-form-item label="任务名">
                  <span>{{ props.row.task_name }}</span>
                </el-form-item>
                <el-form-item label="需求人员">
                  <span>{{ props.row.demand_people }}</span>
                </el-form-item>
                <el-form-item label="翻译语言">
                  <span>{{ props.row.translation_language }}</span>
                </el-form-item>
                <el-form-item label="紧急程度">
                  <span style="color: #00a0e9">{{ props.row.urgency_degree }}</span>
                </el-form-item>
                <el-form-item label="翻译人员">
                  <span>{{ props.row.translate_people }}</span>
                </el-form-item>
                <el-form-item label="设计师">
                  <span>{{ props.row.designer }}</span>
                </el-form-item>
                <el-form-item label="状态">
                  <span style="color: #00a0e9">{{ props.row.status }}</span>
                </el-form-item>
                <el-form-item label="不通过原因" v-if="props.row.unpass_reason">
                  <span>{{ props.row.unpass_reason }}</span>
                </el-form-item>
              </el-form>
            </template>
          </el-table-column>
          <el-table-column
            prop="task_id"
            label="任务id">
          </el-table-column>
          <el-table-column
            prop="task_name"
            label="任务名">
          </el-table-column>
          <el-table-column
            prop="demand_people"
            label="需求人员">
          </el-table-column>
          <el-table-column
            class-name="markColor"
            prop="urgency_degree"
            :filters="[{text: '高', value: '2'}, {text: '中', value: '1'}, {text: '底', value: '0'}]"
            :filter-method="filterDegree"
            label="紧急程度">
          </el-table-column>
          <el-table-column
            class-name="markColor"
            prop="status"
            :filters="[{text: '新增', value: '0'}, {text: '翻译通过', value: '1'}, {text: '翻译待审核', value: '2'}, {text: '翻译不通过', value: '3'}, {text: '翻译进行中', value: '4'}]"
            :filter-method="filterStatus"
            label="状态">
          </el-table-column>
          <el-table-column
            label="操作">
            <template slot-scope="scope">
              <el-button @click="handleEdit(scope.row)" type="text" size="small">编辑</el-button>
              <el-button @click="handleDelete(scope.row,scope.$index)" type="text" size="small">删除</el-button>
            </template>
          </el-table-column>
          <el-table-column
            prop="download"
            label="完成结果">
            <template slot-scope="scope">
              <a :href="scope.row.translate_url" v-if="scope.row.translate_url">下载翻译文件</a>
              <a :href="scope.row.design_url" v-if="scope.row.design_url">下载设计文件</a>
            </template>
          </el-table-column>
        </el-table>
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page="currentPage"
          :page-sizes="[100, 200, 300, 400]"
          :page-size="pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="totalSize">
        </el-pagination>
      </div>
    </div>
</template>

<script>
  const language = {
    'CN': '简体中文',
    'HK': '繁体中文-香港',
    'TW': '繁体中文-台湾',
    'EN': '英语',
    'ID': '印尼语',
    'TH': '泰语',
    'MY': '马来语',
    'UA': '阿拉伯语'
  }
  const degree = ["低","中","高"];
  const statusArr = ['新增','翻译通过','翻译待审核','翻译不通过','翻译进行中'];
  export default {
    name: "submitDemand",
    data() {
        return {
          tableData: [],
          totalSize: 0,
          currentPage: 1,
          pageSize: 100,
          statusValue: '-1',
          degreeValue: '-1',
          statusOptions: [
            {
              value: '-1',
              label: '所有状态'
            },
            {
              value: '0',
              label: '新增'
            }, {
              value: '1',
              label: '翻译通过'
            }, {
              value: '2',
              label: '翻译待审核'
            }, {
              value: '3',
              label: '翻译不通过'
            }, {
              value: '4',
              label: '翻译进行中'
            }
          ],
          degreeOptions: [
            {
              value: '-1',
              label: '所有等级'
            },
            {
              value: '0',
              label: '低'
            }, {
              value: '1',
              label: '中'
            }, {
              value: '2',
              label: '高'
            }
          ]
        }
    },
    methods: {
      degreeChange(val) {
        if(val >= 0){
          if(this.statusValue >= 0){
            this.initTable(val,this.statusValue);
          }else {
            this.initTable(val,null);
          }
        }else {
          if(this.statusValue >= 0){
            this.initTable(null,this.statusValue);
          }else {
            this.initTable();
          }
        }
      },
      statusChange(val) {
        if(val >= 0){
          if(this.degreeValue >= 0){
            this.initTable(this.degreeValue,val);
          }else{
            this.initTable(null,val);
          }
        }else {
          if(this.degreeValue >= 0){
            this.initTable(this.degreeValue,null);
          }else{
            this.initTable();
          }
        }
      },
      filterDegree(value, row, column) {
        const property = column['property'];
        return row[property] === this.degreeOptions[parseInt(value)+1].label;
      },
      filterStatus(value, row, column) {
        const property = column['property'];
        return row[property] === this.statusOptions[parseInt(value)+1].label;
      },
      handleSizeChange(val) {
        this.pageSize = val;
        this.initTable();
      },
      handleCurrentChange(val) {
        this.currentPage = val;
        this.initTable();
      },
      addTask() {
        this.$router.push('/taskManage/addTask')
      },
      handleEdit(row) {
        this.$router.push({ path: '/taskManage/updateTask',query: row})
      },
      handleDelete(row,index) {
        let url = '/v1/translations/'+row.task_id;
        let that = this;
        this.$confirm('是否确认删除该条记录?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          that.$axios.delete(url).then((res) => {
            if(res.data.code == 200){
              that.tableData.splice(index,1);
              that.$message({
                type: 'success',
                message: '删除成功!'
              });
            }else {
              this.$message({
                type: 'error',
                message: '删除失败!'
              });
            }
          }).catch(() => {
            this.$message({
              type: 'error',
              message: '删除失败!'
            });
          })
        }).catch(() => {});
      },
      initTable(level,status) {
        let initList = [];
        let url = "/v1/translations?page="+this.currentPage+"&pagesize="+this.pageSize;
        if(level){
          url += "&level="+level;
        }
        if(status){
          url += "&status="+status;
        }
        let that = this;
        this.$axios.get(url).then((res) => {
          if(res.data.code == 200){
            let tasksList = res.data.data.list;
            that.totalSize = parseInt(res.data.data.page_total);
            tasksList.forEach((task) => {
              let tempTask = {};
              tempTask.task_id = task.id;
              tempTask.task_name = task.title;
              tempTask.demand_people = task.username;
              tempTask.translation_language = language[task.t_language];
              tempTask.urgency_degree = degree[task.level];
              tempTask.translate_people = task.fix_name;
              tempTask.designer = task.designer;
              tempTask.status = statusArr[task.status];
              tempTask.translate_url = task.translate_url;
              tempTask.design_url = task.design_url;
              tempTask.unpass_reason = task.unpass_reason;
              initList.push(tempTask);
            })
          }
          that.tableData = initList;
        }).catch((err) => {})
      }
    },
    created() {
      this.initTable();
    }
  }
</script>

<style scoped lang="stylus">
  .container
    padding 30px
    position relative
    .tools
      margin 20px 0
      .selected
        width: 150px
        margin-right: 20px
      .addItem
        float: right
    .tableContainer
      position absolute
      left 30px
      right: 30px
      top: 120px
      bottom: 0
      display flex
      flex-direction column
      .table
        flex 1
        .demo-table-expand
          font-size: 0
          label
            width: 90px
            color: #99a9bf
          .el-form-item
            margin-right: 0
            margin-bottom: 0
            width: 33.333%
        td.markColor
          color #00a0e9
        a
          color #03B8CC
      .el-pagination
        text-align right
        margin 8px 0
</style>



